import { withPrefix } from 'gatsby';
import React from 'react';
import { Trans } from 'react-i18next';
import { Link, withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import { Page404, Wrapper } from '../ui/utilities';

const NotFoundPage = ({ t, ...context }) => {
  const artists = <Link to={`/${t('routes.artists')}`}>placeholder</Link>;
  const artshop = <Link to={`/${t('routes.artists')}`}>placeholder</Link>;
  const restoration = <Link to={`/${t('routes.restoration')}`}>placeholder</Link>;
  const frames = <Link to={`/${t('routes.frames')}`}>placeholder</Link>;
  const framing = <Link to={`/${t('routes.frames')}`}>placeholder</Link>;

  return (
    <PageWrapper title={t('404.title')} {...context}>
      <Page404>
        <img src={withPrefix('/assets/img/404.png')} aria-hidden='true' alt='' />
        <div className='title'>404</div>
      </Page404>
      <Wrapper reader>
        <h1 className='t-h2'>{t('404.title')}</h1>
        <Trans
          i18nKey='404.description'
          values={{
            artists: t('404.artists'),
            artshop: t('404.artshop'),
            restoration: t('404.restoration'),
            frames: t('404.frames'),
            framing: t('404.framing'),
          }}
          components={[ artists, artshop, restoration, frames, framing ]}
        />
      </Wrapper>
    </PageWrapper>
  );
};

export default withIntl()(NotFoundPage);
